<template>
  <div class="container " style="min-height: 597px;">
    <div class="block-list address-list section section-first js-no-webview-block" v-if="lists&&lists.length">
      <a class="block-item js-address-item address-item"
        v-for="list in lists"
        :key="list.id"
        :class="{'address-item-default':list.isDefault}"
        @click="toEdit(list)"
      >
        <div class="address-title">{{list.name}} {{list.tel}}</div>
        <p>{{list.provinceName}}{{list.cityName}}{{list.districtName}}{{list.address}}</p>
        <a class="address-edit"></a>
      </a>
    </div>
    <div v-if="lists&&!lists.length">
      
    </div>
    <div class="block stick-bottom-row center">
      <router-link class="btn btn-blue js-no-webview-block js-add-address-btn" 
        :to="{name: 'from', query:{type: 'add'}}"
      >
        
      </router-link>
    </div>
  </div>
</template>

<script>
  export default {
    computed: {
      lists(){
        return this.$store.state.lists
      }
    },
    created(){
      if(!this.lists){
        this.$store.dispatch('getlists')
      }
    },
    methods: {
      toEdit(list){
        this.$router.push({name: 'from', query: {
          type: 'edit',
          instance: list
        }})
      }
    }
  }
</script>



